import React from 'react';

interface TextValueModelProps {
    value: number;
    label: string;
    valueColor?: string;
}

export default function TextValueModel({ value, label, valueColor }: TextValueModelProps) {
    return (
        <div style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
            width: '100%',
        }}>
            <h3 style={{ paddingBottom: '1rem', paddingLeft: '1rem', margin: 0 }}>{label}:</h3>
            <h1 style={{ paddingBottom: '1rem', paddingLeft: '1rem', margin: 0, color: valueColor }}>{value}</h1>
        </div>
    );
}